import React from "react";
import { Route, Redirect } from "react-router-dom";

import Preview from "./preview/pages/Preview";
import "./AppUser.css";
import Cours from "./dashboard/cours/pages/Cours";
import Videos from "./dashboard/cours/pages/Videos";
import Bibliotheque from "./dashboard/bibliotheque/pages/Bibliotheque";
import Quiz from "./dashboard/quiz/pages/Quiz";
import { deviceDetect } from "react-device-detect";

const AppUser = ({ match }) => {
  return (
    <div className="app-user">
      <Route path={match.url} exact component={Preview} />
      <Route path={`${match.url}cours`} exact component={Cours} />
      <Route path={`${match.url}cours/videos`} exact component={Videos} />
      <Route path={`${match.url}bibliotheque`} exact component={Bibliotheque} />
      <Route path={`${match.url}quiz`} exact component={Quiz} />
      <Redirect to="/" />
    </div>
  );

  // return <div className="app-user">{props.children}</div>;
};

export default AppUser;
